import * as React from "react";
import { createHashRouter, RouterProvider, Route } from "react-router-dom";

import ErrorPage from "./error-page";
import Root from "./routes/Root";
import DashBoard from "./routes/DashBoard"
import EventRegistration from "./routes/EventRegistration"
import Souvenir from "./routes/Souvenir"
import Sponsor from "./routes/Sponsor"
import WebsiteTraffic from "./routes/WebsiteTraffic";
import EditArticle from "./routes/EditArticle";
import Test from "./routes/Test";
// const router = createHashRouter([
// const router = createBrowserRouter([ github.io
// createBrowserRouter:github.io
// github.io account.github.io/respostiory <-/respostiory
//  basename={ process.env.PUBLIC_URL }
const router = createHashRouter([
	{
		path: "/",
		element: <Root />,
		errorElement: <ErrorPage />,
		children: [
			{
				errorElement: <ErrorPage />,
				children: [
					{ index: true, element: <DashBoard /> },
					{
						path: "DashBoard",
						element: <DashBoard />,
					},
					{
						path: "EventRegistration",
						element: <EventRegistration />,
					},
					{
						path: "Souvenir",
						element: <Souvenir />,
					},
					{
						path: "Sponsor",
						element: <Sponsor />,
					},
					{
						path: "WebsiteTraffic",
						element: <WebsiteTraffic />,
					},
					{
						path: "EditArticle",
						element: <EditArticle />,
					},
					{
						path: "Test",
						element: <Test />,
					},
				],
			},
		],
	},
]);

export default function Routes() {
	return (
		<div>
			<RouterProvider router={router} />
		</div>
	);
}
