import * as React from "react";
import { useEffect, useState } from "react";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Button from "@mui/material/Button";


export default function EditArticle() {
	const [editor, setEditor] = useState(null);

	useEffect(() => {
		if (editor === null) {
			window.ClassicEditor.create(document.querySelector("#editor"))
				.then((res) => {
					setEditor(res);
					console.log(`init complete : ${res}`);
				})
				.catch((error) => {
					console.error(error);
				});
		}
	}, []);

	useEffect(() => {
		console.log(editor);
		if (editor) {
			let gethtmlBtn = document.querySelector("#getHtmlBtn");
			gethtmlBtn.addEventListener("click", () => {
				getEditorContent();
			});
		}
	}, [editor]);

	const getEditorContent = () => {
		refresh(editor.getData());
	};

	const refresh = (html) => {
		let content = document.querySelector("#content");
		content.innerHTML = html;
	};

	return (
		<Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
			<Grid>
				<h1>-</h1>
				<div id="editor">
					<h2>
						<strong>(2)</strong>
					</h2>
					<h3>
						<strong>(3)</strong>
					</h3>
					<h4>
						<strong>(4)</strong>
					</h4>
					<p>
						 <strong>()</strong>
					</p>
					<p>
						<a href="https://jsfiddle.net/kaizz1129/gfrymawx/26/">
							Demo
						</a>
					</p>
				</div>
			</Grid>
			<br />
			<Button variant="contained" id="getHtmlBtn">
				
			</Button>
			<Grid>
				<Paper>
					<div id="content" className={"ck-content"}></div>
				</Paper>
			</Grid>
		</Container>
	);
}
